<template>
  <div class="homeContainer">
    <!-- 广告招商 -->
    <div class="pic" v-show="swiperList.length == 0">广告招商中</div>
    <!-- 广告轮播图 -->
    <div class="Carousel" v-show="swiperList.length != 0">
      <el-carousel :interval="3000" arrow="always" autoplay height="350px">
        <a
          href="https://www.bilibili.com/"
          v-for="swiper in swiperList"
          :key="swiper.did"
        >
          <el-carousel-item>
            <img class="swiperPic" :src="swiper.swiper" />
          </el-carousel-item>
        </a>
      </el-carousel>
    </div>
    <!-- 食堂公告 -->
    <div class="notice">
      <el-card class="box-card">
        <div slot="header" class="ggtitle">
          <span class="">公告</span>
        </div>
        <div
          v-for="notice in noticeList"
          :key="notice.nid"
          class="text item"
          @click="showNotice(notice)"
        >
          {{ notice.title }}
        </div>
      </el-card>
    </div>
    <!-- 公告详情 -->
    <el-drawer title="我是标题" :visible.sync="drawer" :with-header="false">
      <div class="notTitle">{{ noticeDetail.title }}</div>
      <div class="notDate">{{ noticeDetail.noticeDate }}</div>
      <div class="notContent">{{ noticeDetail.content }}</div>
    </el-drawer>
    <!-- 今日菜品推荐 -->
    <div class="today">
      <div class="TJtitle">今日推荐</div>
      <div>
        <div
          v-for="food in todayFoodList"
          :key="food.did"
          class="TJfood"
          @click="goFoodDetail(food.did)"
        >
          <div class="foodPic">
            <img style="width: 100%" :src="food.dimage" />
          </div>
          <div class="foodInfo">
            <div class="foodTitle">{{ food.dname }}</div>
            <div><span class="money">￥</span>:{{ food.dprice }}.00</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getSuggestionAPI,
  getNoticeListAPI,
  getSwiperListAPI,
  getSugFoodListAPI,
} from "@/api/index";
export default {
  name: "home",
  // =============================================以下是data=============================================
  data() {
    return {
      // 存储轮播图数据的列表
      swiperList: [],
      //   公告列表
      noticeList: [],
      //   今日推荐菜品列表
      todayFoodList: [],
      //   公告详情页数据
      noticeDetail: {
        nid: "",
        noticeDate: "",
        content: "",
        title: "",
      },
      //   用于控制公告详情页的显示与隐藏
      drawer: false,
    };
  },
  // =============================================以下是Method=============================================
  methods: {
    // 获取并初始化轮播图列表
    async getSwiperList() {
      const { data: res } = await getSwiperListAPI({
        page: 1,
        dname: "",
        pagesize: 7,
      });
      if (res.code != 0 && res.code != 1)
        return this.$message.error("获取轮播图列表失败！");
      if (res.code == 1) {
        this.swiperList = [];
      } else {
        this.swiperList = res.data;
      }
    },
    // 获取并初始化公告列表
    async getNoticeList() {
      const { data: res } = await getNoticeListAPI({ page: 1, title: "" });
      if (res.code != 0) return this.$message.error("获取公告列表失败！");
      this.noticeList = res.data;
    },
    // 获取并初始化今日推荐菜品列表(随机)
    async getTodayFoodListRandom() {
      const { data: res } = await getSuggestionAPI();
      if (res.code !== 0) return this.$message.error("后去推荐菜品列表失败！");
      this.todayFoodList = res.data;
    },
    // 获取并初始化今日推荐菜品列表(后台管理)

    async getTodayFoodList() {
      const { data: res } = await getSugFoodListAPI({
        page: 1,
        dname: "",
        pagesize: 7,
      });
      if (res.code !== 0 && res.code != 1)
        return this.$message.error("后去推荐菜品列表失败！");
      console.log(res);
      this.todayFoodList = res.data;
    },
    // 浏览公告详情
    showNotice(noticeOBJ) {
      this.drawer = true;
      this.noticeDetail = noticeOBJ;
    },
    // 点击商品详情后跳转至对应的详情页
    goFoodDetail(foodID) {
      this.$router.push({
        name: "Food",
        query: { foodID: foodID },
      });
    },
  },
  // =============================================以下是Create=============================================
  created() {
    this.getTodayFoodList();
    this.getNoticeList();
    this.getSwiperList();
  },
};
</script>

<style lang="less" scoped>
.pic {
  width: 800px;
  height: 350px;
  color: white;
  background-color: rgb(87, 88, 91);
  font-size: 88px;
  font-family: "幼圆";
  font-weight: 600;
  text-align: center;
  line-height: 350px;
  text-shadow: 0px 0px 30px rgb(47, 141, 159);
}
// 轮播图样式
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
// 自定义修改的样式==============================================
.homeContainer {
  position: relative;
  width: 100%;
  height: 100%;
}
// 轮播图==============================================
.Carousel {
  width: 800px;
  position: absolute;
  top: 0px;
  left: 0px;
}
.swiperPic {
  width: 800px;
  height: 350px;
}
// 公告==============================================
.notice {
  position: absolute;
  top: 0px;
  right: 0px;
}
.ggtitle {
  font-size: 20px;
  font-weight: 600;
}
.text {
  font-size: 14px;
}

.item {
  margin-bottom: 5px;
  height: 40px;
  line-height: 40px;
  padding-left: 20px;
  border-radius: 15px;
  cursor: pointer;
}
.item:hover {
  background-color: rgb(242, 242, 242);
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}

.box-card {
  width: 450px;
  height: 350px;
  border-radius: 15px;
}
.notTitle {
  height: 60px;
  margin-top: 30px;
  font-size: 40px;
  font-weight: 600;
  line-height: 60px;
  text-align: center;
  border-bottom: #99a9bf solid 2px;
}
.notDate {
  height: 20px;
  line-height: 20px;
  margin-top: 5px;
  text-align: right;
  padding-right: 10px;
  color: #7d7d7e;
  font-size: 15px;
}
.notContent {
  background-color: #e9ecef;
  height: 550px;
  width: 400px;
  margin: 30px auto;
  font-size: 20px;
  border: #99a9bf solid 3px;
  border-radius: 15px;
}
// 今日推荐==============================================
.today {
  position: relative;
  width: 100%;
  height: 230px;
  left: 0px;
  top: 370px;
  border: #99a9bf solid 1px;
  border-radius: 5px;
}
.TJtitle {
  background-color: rgb(211, 220, 230);
  height: 30px;
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  border-bottom: #99a9bf solid 1px;
}
.TJfood {
  width: 140px;
  height: 185px;
  background-color: antiquewhite;
  margin-left: 38px;
  margin-top: 6px;
  float: left;
}
.TJfood:hover {
  cursor: pointer;
  box-shadow: 3px 3px 5px rgb(185, 187, 189);
}
.foodPic {
  height: 140px;
  width: 100%;
  background-color: peru;
}
.foodTitle {
  padding-left: 5px;
  font-size: 12px;
}
.money {
  color: orange;
  font-weight: 600;
  font-size: 20px;
}
</style>
